@import url(https://fonts.googleapis.com/css2?family=Dancing+Script:wght@400;500&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Architects+Daughter&display=swap);
*{margin:0;padding:0;box-sizing:border-box}body{background:#ccc}@media screen and (max-width: 850px){body{background:#343434}}

.app{background-color:#343434;display:flex;align-items:center;justify-content:center;flex-direction:column;padding:2em 0;width:70vw;margin:5vh auto;text-align:center;border-radius:10px;color:#83b;text-shadow:1px 1px 0 black;box-shadow:0px 0px 7px 1px #000000}.prog-title{font-family:'Dancing Script', cursive;font-size:5em;color:aliceblue;text-shadow:2px 2px 2px #a5b}button{font-family:'Dancing Script', cursive;background:white;color:#a5b;height:2em;width:3em;border:none;border-radius:5px;font-size:1.2em;font-weight:bold;text-align:center}button:disabled{background-color:grey}.section-container{font-family:'Architects Daughter', cursive;display:grid;grid-template-columns:1fr 1fr;grid-gap:5em;padding-top:2em}@media screen and (max-width: 850px){.app{width:100%;margin:0 auto;margin-top:10vh;margin-bottom:10vh}.prog-title{font-size:2em}button{height:100%;width:1.5em}.section-container{width:80%;padding-top:2em;grid-gap:1em;gap:1em}}

.break-section{display:flex;flex-direction:column}.break-button-container{display:grid;grid-template-columns:2fr 3fr 2fr;align-items:center;justify-content:center;justify-items:center;align-content:center;padding:1em 0}#break-label{font-size:2em}#break-length{font-size:3em}@media screen and (max-width: 850px){#break-label{font-size:1em}#break-length{font-size:1.5em}}

.session-section{display:flex;flex-direction:column}.session-button-container{display:grid;grid-template-columns:2fr 3fr 2fr;align-items:center;justify-content:center;justify-items:center;align-content:center;padding:1em 0}#session-label{font-size:2em}#session-length{font-size:3em}@media screen and (max-width: 850px){#session-label{font-size:1em}#session-length{font-size:1.5em}}

.display-section{display:flex;flex-direction:column;width:100%;justify-content:center;align-items:center}.display-timer-container{font-family:'Architects Daughter', cursive;border:2px solid #bddbf5;padding:2em 0;width:80%;border-radius:5px;margin-bottom:5em;background-color:#8c42b8;color:white}#timer-label{font-size:3em}#time-left{font-size:10em}.display-button-container{display:grid;grid-template-columns:1fr 1fr;width:80%;justify-items:center;align-content:center}#reset,#start_stop{font-size:1em;height:3em;width:7em}@media screen and (max-width: 850px){.display-timer-container{width:80%;margin-bottom:5em}#timer-label{font-size:2em}#time-left{font-size:5em}}

